import React from 'react';

function Career() {
  return (
    <div
      className="col-md-12"
      style={{
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'noRepeat',
        height: '1080px',
        opacity: '50%',
        paddingTop: '50px',
      }}
    >
      <h1>This is red car</h1>
    </div>
  );
}

export default Career;
