import React from 'react';
import { Carousel, Container } from 'react-bootstrap';
import FadeIn from 'react-fade-in';
import about from '../images/about.png';
import career from '../images/career.jpg';
import { Link } from 'react-router-dom';
import white from '../images/white.jpg';
import contact from '../images/contact.jpg';
function Home() {
  return (
    <div>
      <FadeIn>
        <div
          className="col-md-12"
          style={{
            backgroundImage: `url(${white})`,
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundRepeat: 'noRepeat',
            paddingTop: '50px',
            paddingBottom: '30px',
            color: 'gray',
          }}
        >
          <Container>
            <div className="container-header">
              <text style={{ fontSize: '40px' }}>Ad Astra Abyssosque</text>
            </div>
            <br />
            <div className="container-content" style={{ paddingTop: '10px' }}>
              <text style={{ textAlign: 'justify' }}>
                We can't shares our love to a stranger, right? So let me
                introduce my self.
                <div className="row">
                  <div className="col-md-1"> </div>
                  <div
                    className="col-md-10"
                    style={{ textAlign: 'justify', textAlignLast: 'center' }}
                  >
                    <br /> My name is Rama, and this is my humble place to
                    shares my interests. i'm a software engineer for a living,
                    and i love the hell out of doing it. You have a problem
                    resolving your issue? Have at me, i'll try my best helping
                    you out. Of course, what is a man without any hobbies,
                    right? i'll always love to play guitar when ever i had spare
                    times. Favorite genre? well you could say i swinging on
                    Metal, speacially Djent. but i also love to play acoustic
                    time to time, since it kinda resolving peace inside my mind.
                  </div>
                  <div className="col-md-1"></div>
                </div>
              </text>
            </div>
          </Container>
          <Carousel
            style={{
              width: '50%',
              margin: 'auto',
              paddingTop: '25px',
              marginBottom: '25px',
            }}
          >
            <Carousel.Item interval={5000}>
              <Link to="/about">
                <img width="50%%" className="d-block w-100" src={about} />
              </Link>
              <Carousel.Caption>
                <h3>About Me</h3>
                <p>Find out everything you need to know about me</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item interval={5000}>
              <Link to="/career">
                <img width="50%%" className="d-block w-100" src={career} />
              </Link>
              <Carousel.Caption>
                <h3>My Career</h3>
                <p>My work's traces up until now</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item interval={5000}>
              <Link to="/about">
                <img width="50%%" className="d-block w-100" src={contact} />
              </Link>
              <Carousel.Caption>
                <h3>Contact</h3>
                <p>Here some ways to contact me</p>
              </Carousel.Caption>
            </Carousel.Item>
          </Carousel>
        </div>
      </FadeIn>
    </div>
  );
}

export default Home;
